.outLink{
  display: inline-block;
  position: relative;
}

.outLink:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #404040;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.outLink:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

